import React from 'react';
import Stepper from 'react-stepper-horizontal/lib/Stepper';

const Level = ({score,level,countLevel}) => {
    const tab = []

    level.map((element) => {
        return tab.push({title : element});
    })

    return (
        <div className='containerLevel'>
            <div>
                <Stepper 
                steps={ tab } 
                activeStep={ countLevel } 
                activeColor= {"red"}
                completeColor ={"green"}
                completeBarColor={"green"}	
                size ={45}
                defaultTitleColor ={"white"}
                completeTitleColor ={"green"}
                activeTitleColor={"red"}
                />
            </div>
            <div className='score'>+|{score}</div>
        </div>
    );
};

export default React.memo(Level);