import React from 'react';
import { Link } from 'react-router-dom';

const Landing = () => {

    const myref = React.createRef()

    const handleMouseOverL = ()=>{
        myref.current.classList.add("leftImg");
    }

    const handleMouseOverR = ()=>{
        myref.current.classList.add("rightImg");
    }

    const handleMouseOut = (e)=>{
        if(myref.current.classList.contains("leftImg")){
            myref.current.classList.remove("leftImg")
        }else if(myref.current.classList.contains("rightImg")){
            myref.current.classList.remove("rightImg")
        }
    }
    return (
        <div className='Landing' ref={myref} >
            <div className='landingitem ll'  onMouseOver={handleMouseOverL} onMouseOut={handleMouseOut}>
                <Link to="/signup">
                    <button className='btn'>Inscription</button>
                </Link></div>
            <div className="landingitem lr" onMouseOver={handleMouseOverR} onMouseOut={handleMouseOut}>
                <Link to="/login" >
                    <button className='btn'>Login</button>
                </Link></div>
        </div>
    );
};

export default Landing;