import React, { useEffect, useState} from 'react';
import Level from "./Level"
import ProgressBar from './ProgressBar';
import QuizContent from './QuizContent';
import {QuizMarvel} from "./MarvelQuiz"
import GameOver from '../GameOver';
import {toast} from "react-toastify"
import "react-toastify/dist/ReactToastify.css"

toast.configure()
const dataRef = React.createRef(null)

const Quiz = ({userData}) => {
    const [level, setLevel] =useState(["debutant", "confirme", "expert"])
    const [countLevel, SetCountLevel] = useState(0)
    const [dataQuiz, setDataQuiz] = useState({})
    const [idquestion, setIdquestion] = useState(0)
    const [goodanswer, setGoogAnswer] = useState("")
    const [gameOver, setGameOver] = useState(false)
    const [score, setScore] = useState(0)
    const [progress, setProgress] = useState(1)
    const [btnDisabled, setBtnDisabled] = useState(true)
    const maxQuestion = 9;
    const [welcomemsg, setWelcomeMsg] = useState(false)

    useEffect(() => {
        const nieau = level[countLevel]
        const fetchdata = QuizMarvel[0].quizz[nieau]

        dataRef.current = fetchdata

        const newdata = fetchdata.map(({answer, ...element}) => {
            return element;
        })
        if(newdata){
            setDataQuiz(newdata)
            setGoogAnswer(fetchdata[idquestion].answer);
        }

        if(!welcomemsg){
            if(userData.pseudo){
                toast.info(`Bienvenue ${userData.pseudo}`, {
                    position:"top-left",
                    autoClose:5000,
                    hideProgressBar:false,
                    closeOnClick:true,
                    pauseOnHover:true,
                    draggable:false,
                    progress:undefined,
                })
            }
            setTimeout(() => {
                setWelcomeMsg(true)
            }, 5000)
        }

    }, [countLevel, level,idquestion, welcomemsg, userData])

    const toastnotify = (ganswer, useranswer) => {
        if(ganswer === useranswer){
            setScore(o => o + 1)
            toast.success("Bravo", {
                position:"top-right",
                autoClose:1000,
                hideProgressBar:false,
                closeOnClick:true,
                pauseOnHover:true,
                draggable:false,
                progress:undefined,
            })
        }else{
            toast.error("Echec", {
                position:"top-right",
                autoClose:1000,
                hideProgressBar:false,
                closeOnClick:true,
                pauseOnHover:true,
                draggable:false,
                progress:undefined,
            })
        }
    }
    const handleNextQuestion = (ganswer, useranswer) => {
        if(idquestion < maxQuestion){
            setIdquestion(c => c + 1 )
            setProgress(val => val + 1)
            setBtnDisabled(true)
            toastnotify(ganswer, useranswer)

        }else if(idquestion === maxQuestion){
            toastnotify(ganswer, useranswer)
            setTimeout(() => {
                setGameOver(true)
            },1000)
        }
    }
    const handleNextLevel = (val) =>{
            if(countLevel < 2 && score > (maxQuestion +1) /2 ){
                SetCountLevel(count => count + 1)
            }else if(countLevel === 2){
                SetCountLevel(0)
            }
            setIdquestion(0)
            setScore(0)
            setProgress(1)
            setGameOver(false)
            setBtnDisabled(true)
    }


    return  gameOver ? 
    (
        <>
            <GameOver 
            ref={dataRef.current} 
            score={score} handleNextLevel={handleNextLevel} 
            countLevel={countLevel}
            maxQuestion={maxQuestion}
            />
        </>
    ) : 
    (
        <>
            <Level score={score} level={level} countLevel={countLevel}/>
            <ProgressBar progress={progress} />
            <QuizContent 
            dataQuiz={dataQuiz[idquestion]} 
            goodanswer={goodanswer} 
            nextq={handleNextQuestion} 
            setBtnDisabled = {(val) => setBtnDisabled(val)}
            btnDisabled = {btnDisabled}
            />
        </>
    )
};

export default React.memo(Quiz);