import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import FirebaseContext from '../Firebase';
import ErroMsg from "../ErrorMsg"

const Signup = () => {

    const firebase = useContext(FirebaseContext)
    const navigate = useNavigate()
    const [msg, setMsg] = useState("")

    const [infoUser, SetInfoUser] = useState({
        pseudo : "",
        email : "",
        pw : "",
        confirmpw : "",
        BtnDisabled : true
    })

    const handleChange = (e) => {
        SetInfoUser({...infoUser, [e.target.id] : e.target.value})
        setMsg("")
    }

    const {pseudo, email, pw, confirmpw, BtnDisabled} = infoUser

    useEffect(() => {
        if(!!pseudo && !!email){
            if(pseudo.length > 3 && email.length >10 && pw !== "" && pw === confirmpw){
                SetInfoUser({...infoUser, BtnDisabled : false})
            }else{
                SetInfoUser({...infoUser, BtnDisabled : true})
            }
        }
    },[pseudo, email, pw, confirmpw, BtnDisabled])

    const handleSubmit = (e) => {
        e.preventDefault()
        firebase.userSignup(email, pw)
        .then((user) => {
            firebase.addUsers(pseudo, email, user.user.uid)
        })
        .then(() => {
            navigate("/welcome")
        })
        .catch((error) => {
            setMsg(error.message)
            SetInfoUser({...infoUser})
        })
    }

    return (
        <div className='signupContainer'>
            <div className='SignupItem Sleft'></div>
            <div className='SignupItem Sright'>
                <h3>INSCRIPTION</h3>
                    {<ErroMsg msg={msg}/>}
                <form className='formContainer'>
                    <div className='formItem'>
                        <input type="text" id='pseudo' value={pseudo} onChange={handleChange}/>
                        <label htmlFor='pseudo'>Pseudo</label>
                    </div>
                    <div className='formItem'>
                        <input type="email" id='email' value={email} onChange={handleChange} required/>
                        <label htmlFor='email'>Email</label>
                    </div>
                    <div className='formItem'>
                        <input type="password" id='pw' value={pw} onChange={handleChange} />
                        <label htmlFor='pw'>Mot de passe</label>
                    </div>
                    <div className='formItem'>
                        <input type="password" id='confirmpw' value={confirmpw} onChange={handleChange} />
                        <label htmlFor='confirmpw'>Confirmer votre mot de passe</label>
                    </div>
                    <div className='formItem' >
                        <div className='btnCenter'>
                            <button type="submit" 
                                disabled={BtnDisabled} 
                                className='btn'
                                onClick={handleSubmit}
                            >Inscription
                            </button>
                        </div>
                    </div>
                    <hr/>
                    <Link to="/login"><p>Déja inscrit ? Connectez-vous ici.</p></Link>
                </form>
            </div>
        </div>
    );
};

export default React.memo(Signup);