import React from 'react';

const ProgressBar = ({progress}) => {
    return (
        <div className='containerProgress'>
            <div>
                <div className='progItem'>Niveau : {progress}/10</div>
                <div className='progItem'>Progression {progress * 10}%</div>
            </div>
            <div className='pbarContainer'>
                <div className='progresbar'>
                    <div className='bar' style={{width : `${progress *10 }%`}} ></div>
                </div>
            </div>
        </div>
    );
};


export default ProgressBar;