import { initializeApp } from "firebase/app";
import {getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, onAuthStateChanged, signOut, sendPasswordResetEmail} from "firebase/auth"
import { getFirestore, doc, setDoc, getDocs, collection } from 'firebase/firestore/lite';

const firebaseConfig = {
    apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
    authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
    projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
    storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
    messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
    appId: process.env.REACT_APP_FIREBASE_APP_ID
};


class Firebase{
    constructor(){
        this.app = initializeApp(firebaseConfig);
        this.auth = getAuth()
        this.db = getFirestore(this.app);
    }
    // inscription
    userSignup = (email, password) => createUserWithEmailAndPassword(this.auth, email, password)
    
    // authentification
    userLogin = (email, password) => signInWithEmailAndPassword(this.auth, email ,password)
    

    // deconnexion
    userLogOut = () => {
        return signOut(this.auth)
    }

    // statut connexion
    userConnect = (nav, set) => onAuthStateChanged(this.auth, (user) => {
        if(user){
            setTimeout(() => {
                set(user)
            }, 1000)
        }else{
            nav("/")
        }
    })
    

    // réinitialisation mot de passe
    userForgetPassWord = (email) => sendPasswordResetEmail(this.auth, email) 

    // firestore
    getUsers = async(ido, st) => { 
        const usersCollection = collection(this.db, "users")
        const users = await getDocs(usersCollection);
        const data = users.docs.map((user) => ({...user.data(), id : user.id}))
        return data.map(data => {if(data.id === ido){st(data)}})
    }
    

    addUsers = async (ps, em,id) => {
        return setDoc(doc(this.db, "users", `${id}`), {
            pseudo : ps,
            email : em
        })
    }
}

export default Firebase;