import React, { forwardRef, useEffect, useState} from 'react';
import Loader from "../Loader"
import Modal from '../Modal';
import axios from "axios"

const GameOver = forwardRef((props, ref) => {
    const {score,handleNextLevel, countLevel, maxQuestion} = props
    const [newdata, setNewData] = useState(null)
    const [showModal, setChowModal] = useState(false)
    const [modalData, setModalData] = useState(null)

    useEffect(() => {
        setNewData(ref)
        handleLocalDate(localStorage.getItem("localstorageData"))
    },[ref,modalData])

    const handleLocalDate = (date) => {
        const newDate = Date.now();
        const timeStampeDateDifference = newDate - date;
        const daysdifference = timeStampeDateDifference/(1000*3600*24)
        if(daysdifference >= 15){
            localStorage.clear()
            localStorage.setItem("localstorageData", Date.now())
        }
    }

    const API_MARVEL = process.env.REACT_APP_MARVEL_PUBLIC_KEY
    const hash = process.env.REACT_APP_MARVEL_HASH

    const handleInfo = (id) => {
        setChowModal(true)

        if(localStorage.getItem(id)){
            setModalData(JSON.parse(localStorage.getItem(id)))
        }else{
            axios
            .get(`https://gateway.marvel.com/v1/public/characters/${id}?ts=1&apikey=${API_MARVEL}&hash=${hash}`)
            .then((response) => {
                setModalData(response.data.data.results[0])

                    if(!localStorage.getItem("localstorageData")){
                        localStorage.setItem("localstorageData", Date.now())
                    }
                    localStorage.setItem(id, JSON.stringify(response.data.data.results[0]))
            })
            .catch((error) => {
                console.log(error);
            })
        }
    }

    const handleCloseModal = ()=>{
        setChowModal(false)
    }

    const displayModal = showModal && 
    (
        <Modal handleCloseModal={handleCloseModal} modalData={modalData && modalData} />
    )

    const tr = !!newdata && (
        newdata.map(element => (
            <tr key={element.id}>
                <td>{element.question}</td>
                <td>{element.answer}</td>
                <td><div><button className='btn' onClick={() => handleInfo(element.heroId)}>Info</button></div></td>
            </tr>
        ))
    )

    const average = (maxQuestion +1)/2


    const displayMsg = score >= average ? 
    (
        <>
            <div className='gamesms'>
                {
                    countLevel === 2 ? 
                    (
                        <>
                            <h2>Bravo vous êtes un expert</h2>
                            <div><button className="btn" onClick={handleNextLevel}>Terminé</button></div>
                        </>
                    ) : 
                    (
                        <>
                            <h2>Bravo continuez comme ça ...</h2>
                            <div><button className="btn" onClick={handleNextLevel}>Niveau suivant</button></div>
                        </>
                    )
                }
            </div>
        </>
    ) : 
    (
        <>
            <div className='gamesms'>
                <h2>Echoué, merci de reéssayer</h2>
                <div><button className="btn" onClick={handleNextLevel}>Rejouer</button></div>
            </div>
        </>
    )

    const diplayTable = score < average ? 
    (
        <tr>
            <td colSpan="3">
                <Loader texte="Pas de data disponible ..." color="red" />
            </td>
        </tr>
    ) : 
    (
        <>
            {tr}
        </>
    )

    return (
        <div className='gameoverContainer'>
            <div className='containerProgress'>
                {displayMsg}
                <div className='containerProgress'>
                    <div>
                        <div className='progItem'>Note {score}/10</div>
                        <div className='progItem'>Progression {score *10}%</div>
                    </div>
                </div>
                <div className='containerProgress'>
                    <div className='infotable'>
                        <table className='table'>
                            <thead>
                                <tr>
                                    <th>Questions</th>
                                    <th>Réponses</th>
                                    <th>Info</th>
                                </tr>
                            </thead>
                            <tbody>
                                {diplayTable}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            {displayModal}
        </div>
    )

    
})

export default React.memo(GameOver);