import React, { useEffect, useState } from 'react';
import Loader from '../Loader';

const Modal = ({handleCloseModal, modalData}) => {
    const [displayModal, setDisplayModal] = useState(false)

    useEffect(() => {
        if(modalData){
            setDisplayModal(true)
        }
    },[displayModal, modalData])

    return !displayModal ? 
    (
        <div className='modalContainer'>
            <div className='modal'>
                <div className='modalHead'>Chargement des infos ...</div>
                <div className='modalBody'>
                    {<Loader texte="Chargement ..." color="green" />}
                </div>
                <div className='modalFooter'>
                    <div>
                        <button className='btn' onClick={handleCloseModal}>Fermer</button>
                    </div>
                </div>
            </div>
        </div>
    ) :  
    (
        <div className='modalContainer'>
            <div className='modal'>
                <div className='modalHead'>{modalData.name}</div>
                <div className='modalBody'>
                    <div className='modalItem'>
                        <img  src={`${modalData.thumbnail.path}.${modalData.thumbnail.extension}`} alt={modalData.name} style={{width : "100%", height : "100%"}} />
                    </div>
                    <div className='modalItem description'>
                        <h3>Description</h3>
                        {modalData.description !== "" ? modalData.description : "Description indisponible ..." }
                        <hr/>
                        <h3>Plus d'info</h3>
                        {modalData.urls && modalData.urls.map((element, index) => {
                            return (
                                <a key={index} href={element.url} target="_blank" rel="noopener noreferrer">{element.type}</a>
                            )
                        })}
                    </div>
                </div>
                <div className='modalFooter'>
                    <div>
                        <button className='btn' onClick={handleCloseModal}>Fermer</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Modal;