import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import ErrorMsg from '../ErrorMsg';
import FirebaseContext from '../Firebase';

const ForgetPassword = () => {

    const [btn, SetBtn] = useState(true)
    const [email, setEmail] = useState("")
    const firebase = useContext(FirebaseContext)
    const navigate = useNavigate()
    const [erromsg, setErrormsg] = useState("")
    const [succesmsg, setSuccessMsg] =  useState("")

    useEffect(()=>{
        if(email){
            if(email.length > 10){
                SetBtn(false)
            }else{
                SetBtn(true) 
            }
        }
    },[email])

    const handleChange = (e) => {
        setEmail(e.target.value)
        setErrormsg("")
    }

    const handleResetEmail = (e) =>{
        e.preventDefault()
        firebase.userForgetPassWord(email)
        .then((user) => {
            setSuccessMsg(`Un mail vient d'être envoyé sur ${email}. Merci de le consulter`)
            setTimeout(() =>{
                navigate("/login")
            }, 5000)
        })
        .catch(error => setErrormsg(error.message))
    }

    const displaySuccessMsg  = succesmsg && (
        <div className='successmsg'>
            <p>{succesmsg}</p>
        </div>
    )

    return (
        <div className='ForgetPsContainer'>
            <div className='forgetItem Sleft'></div>
            <div className='forgetItem Sright'>
                <h3>Récupérer votre mot de passe</h3>
                {<ErrorMsg msg={erromsg} />}
                {displaySuccessMsg}
                <form className='formContainer'>
                    <div className='formItem'>
                        <input type="email" id='email' value={email} onChange={handleChange} required/>
                        <label htmlFor='email'>Email</label>
                    </div>
                    <div className='btnCenter'>
                        <button type="submit" className='btn' onClick={handleResetEmail} disabled={btn}>Récupérer</button>
                    </div>
                    <hr/>
                    <Link to="/login"><p>Déja inscrit ? Connectez-vous ici.</p></Link>
                </form>
            </div>
        </div>
    );
};

export default ForgetPassword;