import React, { useEffect, useState } from 'react';

const QuizContent = (props) => {
    const {dataQuiz, goodanswer, nextq, setBtnDisabled, btnDisabled} = props
    const [question, setQuestion] = useState("")
    const [options, setOptions] = useState([])
    const [useanswer, setUseAnswer] = useState("")

    useEffect(() => {
        if(dataQuiz){
            setQuestion(dataQuiz.question);
            setOptions(dataQuiz.options)
        }
        
    
    }, [dataQuiz])
    const handleSelect = (selection) =>{
        setUseAnswer(selection)
        setBtnDisabled(false)
    }

    const listOption = options.map((element, index) => {
        return (
            <p key={index} className={`OptionQuiz ${useanswer ===  element ? "select" : ""}`}
                onClick={() => handleSelect(element)}
            >
                {element}
            </p>
        )
    })
    return (
        <div className='containerQuiz'>
            <h5>{question}</h5>
            <div className='optionQuestions'>
                {listOption}
            </div>
            <div className='btnCenter'>
                <button className='btn' disabled={btnDisabled} onClick={() => nextq(goodanswer, useanswer)}>Suivant</button>
            </div>
        </div>
    );
};  

export default React.memo(QuizContent);