import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import ErrorMsg from '../ErrorMsg';
import FirebaseContext from '../Firebase';



const Login = () => {
    const [email, setEmail] = useState("")
    const [pw, setPassword] = useState("")
    const [btn, setBtn] = useState(true)
    const [erromsg, setErrormsg] = useState("")
    const firebase = useContext(FirebaseContext)
    const navigate = useNavigate()

    const handleChange = (e) => {
        if(e.target.id === "email"){
            setEmail(e.target.value)
        }else if(e.target.id === "pw"){
            setPassword(e.target.value)
        }
    }

    useEffect(() => {
        if(email !== "" && pw !==""){
            if(email.length > 10){
                setBtn(false)
            }else{
                setBtn(true)
            }
        }else{
            setBtn(true)
        }
        if(erromsg){
            setTimeout(() => {
                setErrormsg('')
            },3000)
        }
    },[email, pw, erromsg])

    const handleConnect = (e) => {
        e.preventDefault()
        firebase.userLogin(email, pw)
        .then((user) => {
            navigate("/welcome");
        })
        .catch((error) => setErrormsg(error.message))
        setPassword("")
        setEmail("")
    }
    return (
        <div className='LoginContainer'>
            <div className='LoginItem Sleft'></div>
            <div className='LoginItem Sright'>
                <h3>CONNEXION</h3>
                {<ErrorMsg msg={erromsg} />}
                <form className='formContainer'>
                    <div className='formItem'>
                        <input type="email" id='email' value={email} onChange={handleChange} required/>
                        <label htmlFor='email'>Email</label>
                    </div>
                    <div className='formItem'>
                        <input type="password" id='pw' value={pw} onChange={handleChange} />
                        <label htmlFor='pw'>Mot de passe</label>
                    </div>
                    <div className="formItem">
                        <div className='btnCenter'>
                            <button type="submit" 
                                className='btn' 
                                disabled={btn} 
                                onClick={handleConnect}
                                >Connexion
                            </button>
                        </div>
                    </div>
                    <hr/>
                    <Link to="/forgetpassword"><p>Mot de passe oublié ? Récupérer-le ici</p></Link>
                </form>
            </div>
        </div>
    );
};

export default Login;